import React from "react";
import "./Assets/input.scss";
function Inputs(props) {
  const {
    width,
    label,
    refs,
    placeholder,
    type,
    errors,
    img,
    icon,
    name,
    isError,
    onClickHandle
  } = props;
  return (
    <React.Fragment>
      <div
        className="COMMON_INPUT"
        style={{
          width,
          borderBottomColor:
            isError === true
              ? errors
                ? "#e26868"
                : "rgba(255,255,255,.25)"
              : ""
        }}
      >
        <label htmlFor={name}>{label}</label>
        <br />
        <input
          ref={refs}
          type={type}
          name={name}
          placeholder={placeholder}
          id={name}
          style={{
            color:
              isError === true
                ? errors
                  ? "#e26868"
                  : "rgba(255,255,255,.25)"
                : ""
          }}
        />
        {img ? (
          <img src={img} alt="" />
        ) : (
          <i
            onClick={onClickHandle}
            className={`fa fa-${icon}`}
            style={{
              color:
                isError === true
                  ? errors
                    ? "#e26868"
                    : "rgba(255,255,255,.5)"
                  : ""
            }}
          ></i>
        )}
      </div>
    </React.Fragment>
  );
}

export default Inputs;
