import React, { useState, useLayoutEffect } from "react";
import { Container, Row, Col } from "reactstrap";
import Input from "../../common/INPUT";
import { useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import "./Assets/passwordReset.scss";
function PasswordReset(props) {
  const { register, errors } = useForm({
    mode: "onBlur",
    defaultValues: {},
    validateCriteriaMode: "firstErrorDetected"
  });
  const onSubmitHandle = e => {
    e.preventDefault();
  };
  const [size, setSize] = useState(window.innerWidth);
  const [width, setWidth] = useState("60%");
  useLayoutEffect(() => {
    function updateSize() {
      setSize(window.innerWidth);
      setWidth(window.innerWidth <= 768 ? "90%" : "60%");
    }
    window.addEventListener("resize", updateSize);
    if (window.innerWidth <= 768) {
      updateSize();
    }
  }, []);
  return (
    <React.Fragment>
      <Container fluid={true} className="RESET_PASSWORD_CONTAINER">
        <Row>
          <Col className="MOBILE_TITLE d-flex d-md-none">
            <img src="img/Logo.png" className="img-fluid" alt="" />
          </Col>
          <Col md={6} className="COVER_TITLE">
            <img
              src={`img/${size <= 786 ? "mobileCover@3x" : "LoginImg@3x"}.png`}
              alt=""
            />
          </Col>
          <Col md={6} className="LOGIN_FORM p-md-0 py-3">
            <img src="img/Logo.png" className="d-md-inline d-none" alt="" />
            <h5>Password Reset</h5>
            <span>
              Let's reset your password so you can get back to watching.
            </span>
            <form style={{ width }} onSubmit={onSubmitHandle}>
              <Input
                width="100%"
                placeholder="Enter Email Here..."
                label={"E-mail"}
                name="email"
                type="email"
                refs={register({ required: true })}
                img="img/check.png"
                errors={errors.email}
                isError={true}
              />
              <span>
                {errors.password || errors.email ? "Invalid Credentials" : ""}
              </span>
              <button>
                <Link to="password-reset-form">Submit</Link>
              </button>
              <Link to="/signup">Donâ€™t have an account? Sign up</Link>
            </form>
          </Col>
        </Row>
      </Container>
    </React.Fragment>
  );
}
PasswordReset.whyDidYouRender = true;
export default PasswordReset;
